
.evaluation {
  padding: 18px 0;
  position: relative;
}
.evaluation .classify {
  padding-bottom: 18px;
  margin: 0 18px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.evaluation .classify .item {
  display: inline-block;
  font-size: 12px;
  padding: 8px 12px;
  line-height: 16px;
  background: rgba(0,160,220,0.2);
  color: #4d555f;
  margin-right: 8px;
}
.evaluation .classify .item .count {
  font-size: 8px;
  padding-left: 2px;
}
.evaluation .classify .item.active {
  color: #fff;
  background: #00a9dc;
}
.evaluation .classify .item.bad {
  background: rgba(77,85,93,0.2);
}
.evaluation .classify .item.badActive {
  background: #4d555d;
}
.evaluation .switch {
  font-size: 12px;
  width: 100%;
  padding: 12px 0 12px 18px;
  color: #93999f;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.evaluation .switch .icon-check_circle {
  font-size: 24px;
  vertical-align: middle;
}
.evaluation .switch .icon-check_circle.on {
  color: #00c850;
}


