
.details {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  background: rgba(7,17,27,0.8);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.details .details-content {
  min-height: 100%;
  width: 100%;
}
.details .details-content .details-main {
  margin-top: 64px;
  padding-bottom: 64px;
}
.details .details-content .details-main .name {
  font-size: 16px;
  font-weight: 700;
  width: 100%;
  color: #fff;
  line-height: 16px;
  text-align: center;
}
.details .details-content .details-main .star-wrapper {
  margin: 16px 11px 28px 0;
  text-align: center;
}
.details .details-content .details-main .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  margin: 0 auto 24px auto;
}
.details .details-content .details-main .title .line {
  display: inline-block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 1px;
  background: rgba(255,255,255,0.2);
  margin: auto;
}
.details .details-content .details-main .title .text {
  padding: 0 12px;
  font-size: 14px;
  font-weight: 700;
}
.details .details-content .details-main .supports {
  padding: 0 0 28px 36px;
}
.details .details-content .details-main .supports .support-item {
  color: #fff;
  padding: 0 6px 12px 16px;
}
.details .details-content .details-main .supports .support-item .text {
  vertical-align: middle;
  font-size: 12px;
  font-weight: 200;
  color: #fff;
  line-height: 12px;
}
.details .details-content .details-main .supports .support-item .icon {
  display: inline-block;
  vertical-align: top;
  width: 16px;
  height: 16px;
  margin-right: 6px;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.details .details-content .details-main .bulletin {
  padding: 0 48px;
  font-size: 12px;
  font-weight: 200;
  color: #fff;
  line-height: 24px;
}
.details .details-close {
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0 auto;
  clear: both;
  font-size: 32px;
  color: rgba(255,255,255,0.5);
}
.details.fade-enter-active,
.details.fade-leave-active {
  transition: opacity 0.5s;
}
.details.fade-enter,
.details.fade-leave-active {
  opacity: 0;
}


