
.header {
  position: relative;
  background: rgba(7,17,27,0.5);
  color: #fff;
  blur: 10px;
  overflow: hidden;
}
.header .content-wrapper {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 24px 12px 18px 24px;
  font-size: 12px;
}
.header .content-wrapper .avatar img {
  border-radius: 2px;
}
.header .content-wrapper .content {
  margin-left: 16px;
}
.header .content-wrapper .content .title {
  margin: 2px 0 8px 0;
  font-size: 16px;
}
.header .content-wrapper .content .title .brand {
  display: inline-block;
  vertical-align: top;
  width: 30px;
  height: 18px;
  background-image: url("img/brand@2x.png");
  background-size: 30px 18px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .title .brand {
    background-image: url("img/brand@3x.png");
}
}
.header .content-wrapper .content .title .name {
  margin-left: 6px;
  font-size: 16px;
  line-height: 18px;
  font-weight: bold;
}
.header .content-wrapper .content .description {
  font-size: 12px;
  margin-bottom: 10px;
}
.header .content-wrapper .content .supports .icon {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  height: 12px;
  margin-right: 4px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
}
.header .content-wrapper .content .supports .text {
  line-height: 12px;
  font-size: 10px;
}
.header .content-wrapper .support-count {
  position: absolute;
  right: 12px;
  bottom: 18px;
  padding: 0 8px;
  height: 24px;
  line-height: 24px;
  border-radius: 14px;
  background-color: rgba(0,0,0,0.2);
  text-align: center;
}
.header .content-wrapper .support-count .count {
  vertical-align: top;
  font-size: 10px;
}
.header .content-wrapper .support-count .icon-keyboard_arrow_right {
  font-size: 10px;
  margin-left: 2px;
  line-height: 24px;
}
.header .bulletin-wrapper {
  position: relative;
  height: 28px;
  line-height: 28px;
  padding: 0 22px 0 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background: rgba(7,17,27,0.2);
}
.header .bulletin-wrapper .bulletin-title {
  display: inline-block;
  vertical-align: top;
  margin-top: 8px;
  width: 22px;
  height: 12px;
  background-image: url("img/bulletin@2x.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .bulletin-wrapper .bulletin-title {
    background-image: url("img/bulletin@3x.png");
}
}
.header .bulletin-wrapper .bulletin-text {
  font-size: 10px;
  vertical-align: middle;
  margin: 0 4px;
}
.header .bulletin-wrapper .icon-keyboard_arrow_right {
  position: absolute;
  font-size: 10px;
  right: 12px;
  top: 8px;
}
.header .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-filter: blur(10px);
          filter: blur(10px);
  z-index: -1;
}


