
.detailsWrapper {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  width: 100%;
  background: #fff;
  transition: all 0.4s ease;
}
.detailsWrapper.move-enter-avtive,
.detailsWrapper.move-leave-active {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.detailsWrapper.move-enter,
.detailsWrapper.move-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.foodDetails .back {
  position: absolute;
  color: #fff;
  top: 12px;
  left: 6px;
  font-size: 20px;
  padding: 10px;
}
.foodDetails .info {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  padding: 18px;
}
.foodDetails .info .title {
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
  line-height: 14px;
}
.foodDetails .info .desc {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0;
  padding-top: 8px;
  font-size: 10px;
  color: #93999f;
  line-height: 10px;
}
.foodDetails .info .desc span:last-child {
  padding-left: 12px;
}
.foodDetails .info .price {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 18px;
  font-size: 14px;
  font-weight: 700;
  color: #f01414;
  line-height: 24px;
}
.foodDetails .info .price .unit {
  font-size: 10px;
  font-weight: normal;
}
.foodDetails .info .price .oldPrice {
  padding-left: 12px;
  font-size: 10px;
  font-weight: normal;
  color: #93999f;
  line-height: 24px;
}
.foodDetails .info .shopCart {
  position: absolute;
  right: 18px;
  bottom: 18px;
  height: 24px;
  text-align: center;
  z-index: 2;
}
.foodDetails .info .shopCart .text {
  box-sizing: border-box;
  height: 100%;
  line-height: 24px;
  color: #fff;
  font-size: 10px;
  padding: 0 12px;
  border-radius: 12px;
  background: #00a0dc;
}
.foodDetails .info .shopCart .text.fade-enter-active,
.foodDetails .info .shopCart .text.fade-leave-active {
  transition: opacity 0.2s;
}
.foodDetails .info .shopCart .text.fade-enter,
.foodDetails .info .shopCart .text.fade-leave-active {
  opacity: 0;
}
.foodDetails .info .cart-control {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.foodDetails .desc {
  padding: 18px;
}
.foodDetails .desc .title {
  font-size: 14px;
  font-weight: 500;
  color: #07111b;
  margin-bottom: 6px;
}
.foodDetails .desc .content {
  font-size: 12px;
  font-weight: 200;
  color: #4d555d;
  line-height: 24px;
  padding: 0 8px;
}
.foodDetails .evaluation .title {
  padding-left: 18px;
  padding-bottom: 18px;
  font-size: 14px;
  font-weight: 500;
  color: #07111b;
}
.foodDetails .evaluation .eval-list {
  margin: 0 18px;
}
.foodDetails .evaluation .eval-list .eval {
  padding: 16px 0;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.foodDetails .evaluation .eval-list .eval .userInfo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: #93999f;
  font-size: 10px;
  line-height: 12px;
}
.foodDetails .evaluation .eval-list .eval .userInfo .time {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.foodDetails .evaluation .eval-list .eval .userInfo .user {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: right;
}
.foodDetails .evaluation .eval-list .eval .userInfo .user .avatar img {
  padding-left: 6px;
  border-radius: 50%;
}
.foodDetails .evaluation .eval-list .eval .content {
  padding-top: 6px;
}
.foodDetails .evaluation .eval-list .eval .content .icon {
  font-size: 12px;
  line-height: 24px;
}
.foodDetails .evaluation .eval-list .eval .content .icon.icon-thumb_up {
  color: #00a0dc;
}
.foodDetails .evaluation .eval-list .eval .content .icon.icon-thumb_down {
  color: #93999f;
}
.foodDetails .evaluation .eval-list .eval .content .text {
  font-size: 12px;
  color: #07111b;
  line-height: 16px;
  padding-left: 4px;
}


